<script>
import myCell from './myCell'
import { apiGetUser } from '@/api/use.js'
import { mapState } from 'vuex'
import { localDel } from '@/utils/mylocal.js'
export default {
  components: {
    myCell
  },
  data () {
    return {
      myList: {}
    }
  },
  methods: {
    initMy () {
      apiGetUser()
        .then(res => {
          console.log(res)
          this.myList = res.data.data
          this.$store.commit('setUserInfo', this.myList)
        })
        .catch(() => {
          this.$toast.fail('我的页面数据初始化失败')
        })
    },
    logout () {
      this.$dialog
        .confirm({
          title: '登 出',
          message: '是否要登出?'
        })
        .then(() => {
          localDel('token')
          this.$router.push('/login')
        })
        .catch(() => {})
    }
  },
  created () {
    this.initMy()
  },
  computed: {
    ...mapState(['userInfo'])
  }
}
</script>

