<template>
  <van-cell is-link :icon='icon' center class="myCell">
    <template #title>
      <div class="title">
        {{ title }}
      </div>
    </template>
    <template #default>
      <!-- 没有头像地址时处理  value===undefined -->
      <!-- type=img且有头像地址 -->
      <img v-if="type === 'img' && value" class="img" :src="value" alt="" />
      <!-- type=img但没有头像地址 -->
      <img
        v-else-if="type === 'img' && !value"
        class="img"
        src="@/assets/logo.png"
        alt=""
      />
      <!-- 非img -->
      <div v-else class="value">
        {{ value }}
      </div>
    </template>
  </van-cell>
</template>

<script>
export default {
  props: ['title', 'value', 'type', 'icon']
}
</script>

<style lang="less">
.myCell {
  width: 100%;
  .img {
    width: 14px;
    height: 14px;
    border-radius: 50%;
  }
}
</style>
